.about{
  margin:0;

  .aboutWrapper{

    .text{
      font-size: 40px;
      color : #00E0FF;
      text-align: center;
      font-weight : bold;

    }
    .section{
      display : flex;
      height : 300px;
      align-items : center;
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;

      .aboutTxt{
        word-break: break-word;
        color : white;
        font-weight : bold;
        text-align: center;
        font-size : 35px;


      }
    }

    .features{
      color:#00E0FF;
      padding: 10px;
      text-align: center;
      margin-top : 2%;
      font-size:40px;
      font-weight : bold;

    }

    .featuresItems{
      padding : 10px;
      display : flex;
      gap : 15px;
      flex-flow : row wrap;
      align-items : center;
      align-content: space-around;

      .item{
        display: flex;
        align-items: center;
        color : white;
        padding: 15px;
        background-color : #00E0FF;
        border-radius : 15px;
        word-wrap : break-word;
        width : 200px;
        height: 300px;
        font-weight: bold;
        font-size : 30px;
        cursor : pointer;
        margin-left : 5%;

        &:hover{
          color:#00E0FF;
          background-color :white;
          border: 1px solid #00E0FF;
        }
      }
    }

  }

}